import { useEffect, useState } from 'react';
import { getGifs } from '../components/helpers/getGifs';


export const useFetchGifs = ( Category ) => {

    const [state, setState] = useState( {
        data: [],
        loading: true
    });

    useEffect( () => { //esta codigo ha si como esta le indica que la sintaxis contenida dentro del hook useEffect 
                       //se ejecutara cuando este en cero ho suceda algun cambio en el array Category, de manera;
        getGifs(Category)      // se renderize el codigo solo cuando sea necesario.
           .then( img => {
             
             setTimeout( () => {
            
               setState({
                   data: img,
                   loading: false
               })

             }, 1500 )
           })

    }, [Category])                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            


    return state;
}